body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: rgba(226, 226, 226, 1); /* Old Browsers */
  background: -moz-linear-gradient(
    left,
    rgba(226, 226, 226, 1) 0%,
    rgba(254, 254, 254, 1) 45%,
    rgba(209, 209, 209, 1) 100%
  ); /* FF3.6+ */
  background: -webkit-gradient(
    left top,
    right top,
    color-stop(0%, rgba(226, 226, 226, 1)),
    color-stop(45%, rgba(254, 254, 254, 1)),
    color-stop(100%, rgba(209, 209, 209, 1))
  ); /* Chrome, Safari4+ */
  background: -webkit-linear-gradient(
    left,
    rgba(226, 226, 226, 1) 0%,
    rgba(254, 254, 254, 1) 45%,
    rgba(209, 209, 209, 1) 100%
  ); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(
    left,
    rgba(226, 226, 226, 1) 0%,
    rgba(254, 254, 254, 1) 45%,
    rgba(209, 209, 209, 1) 100%
  ); /* Opera 11.10+ */
  background: -ms-linear-gradient(
    left,
    rgba(226, 226, 226, 1) 0%,
    rgba(254, 254, 254, 1) 45%,
    rgba(209, 209, 209, 1) 100%
  ); /* IE 10+ */
  background: linear-gradient(
    to right,
    rgba(226, 226, 226, 1) 0%,
    rgba(254, 254, 254, 1) 45%,
    rgba(209, 209, 209, 1) 100%
  ); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e2e2e2', endColorstr='#d1d1d1', GradientType=1 ); /* IE6-9 */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
