.card-container {
  display: flex;
  flex-direction: column;
  background-color: #95c0da0d;
  border: 1px solid #80808054;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
  transition: all 0.25s;
}

.card-container:hover {
  transform: scale(1.02);
}
